import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Components from '@/components/Components'
import Side from '@/components/Sides'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/Components',
      name: 'Components',
      component: Components
    },
    {
      path: '/Side',
      name: 'Side',
      component: Side
    }
  ]
})



